
body, button, input{
  font-family: Arial, Microsoft Yahei;
}
.scoreboard.dragable .scale{
  display: none;
}
.game{
  margin: 0 auto;
  border-spacing: 0;
  border-collapse: collapse;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  text-align: center;
  margin-top: 65px;
}
.game.tips-open{
  margin-top: 97px;
}
.game-config{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  padding: 20px 0;
  background-color: #fff;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}
.game-config label{
  margin-right: 5px;
}
.game-config-tips{
  width: 100%;
  margin-top: 10px;
  text-align: center;
  color: #f36;
  font-weight: bold;
}
td{
  width: 30px;
  height: 30px;
  border: 1px solid;
  padding: 0;
}
.layer{
  position: fixed;
  z-index: 1000;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  text-align: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  font-size: 100px;
  font-weight: bold;
  color: #f36;
  font-family: Arial, Microsoft Yahei;
  background-color: rgba(255, 255, 255, .7);
}
.layer > div{
  width: 100%;
}
.layer .retry a{
  font-size: 40px;
  text-decoration: none;
  color: #06f;
}
.gameover{
  color: #f36;
}
.success{
  color: #09BB07;
}

/* theme */
/* classic */
.classic .game{
  border-collapse: separate;
  font-family: Courier, Arial, Microsoft Yahei;
  font-size: 30px;
  font-weight: bold;
  border-spacing: 1px;
  background-color: #666;
}
.classic .status0{
  width: 30px;
  height: 30px;
  border-style: solid;
  border-width: 5px;
  border-color: #eee #aaa #aaa #eee;
  background-color: #ddd;
  cursor: pointer;
}
.classic .flag{
  cursor: pointer;
  width: 30px;
  height: 30px;
  font-size: 24px;
  color: #f36;
  border-style: solid;
  border-width: 5px;
  border-color: #eee #aaa #aaa #eee;
  background-color: #ddd;
  font-family: FontAwesome;
  font-weight: normal;
  text-rendering: auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.classic .bomb{
  font-size: 20px;
  width: 38px;
  height: 38px;
  border: 1px solid #666;
  background-color: #ccc;
  color: #f36;
  cursor: default;
}
.classic [class^=bomb-count-]{
  width: 38px;
  height: 38px;
  border: 1px solid #666;
  background-color: #ccc;
  cursor: default;
}
.classic .bomb-count-0{
}
.classic .bomb-count-1{
  color: #00f;
}
.classic .bomb-count-2{
  color: #080;
}
.classic .bomb-count-3{
  color: #f00;
}
.classic .bomb-count-4{
  color: #008;
}
.classic .bomb-count-5{
  color: #800;
}
.classic .bomb-count-6{
  color: #088;
}
.classic .bomb-count-7{
  color: #000;
}
.classic .bomb-count-8{
  color: #808;
}




